<template>
    <!-- add-member Modal -->
    <div class="modal fade" id="addMemberModal" tabindex="-1" role="dialog" aria-labelledby="addMemberModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="addMemberModalLabel">Add Team Member</h5>
                    <a class="close" data-dismiss="modal" aria-label="Close" v-on:click="cancelCrop">
                    <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                    </a>
                </div>
                <form v-on:submit.prevent = "addNewTeamMember">
                <div class="modal-body">
                    <div v-show="doCrop">
                        <vue-cropper :aspect-ratio="1 / 1" :view-mode="1" ref="cropper" :src="cropImgUrl" alt="Source Image"></vue-cropper>
                    </div>
                    <div v-show="!doCrop">
                        <div class="user-img">
                            <div class="button-wrapper">
                                <span class="label">
                                <img class="img-fluid" v-bind:src="imgUrl"  alt="" >
                                </span><input type="file" ref="file" accept="image/*" v-on:change="handleFileUpload" class="upload upload-box" placeholder="Upload File">
                            </div>
                            <div class="edit-pro">
                                <img class="img-fluid" src="../../assets/img/edit.svg" alt="">
                            </div>
                        </div>
                        <div class="add-member">
                            <div class="form-row">
                                <div class="form-group col-md-6">
                                    <label for="first_name">First Name</label>
                                    <input v-model="register_.first_name" type="text" class="form-control" id="first_name" placeholder="Jeanne">
                                </div>
                                <div class="form-group col-md-6">
                                    <label for="last_name">Last Name</label>
                                    <input v-model="register_.last_name" type="text" class="form-control" id="last_name" placeholder="Acevedo">
                                </div>
                            </div>
                            <div class="form-group">
                                <label for="email">Email Address</label>
                                <input v-model="register_.email" type="email" class="form-control" id="email" placeholder="example@gmail.com">
                            </div>
                            <div class="form-row">
                                <div class="form-group col-md-6">
                                    <label for="password">Password</label>
                                    <input v-model="register_.password" type="password" class="form-control" id="password" placeholder="Enter password">
                                </div>
                                <div class="form-group col-md-6">
                                    <label for="cpassword">Confirm Password</label>
                                    <input v-model="register_.cpassword" type="password" class="form-control" id="cpassword" placeholder="Again enter password ">
                                </div>
                            </div>
                            <div class="form-group">
                                <label>Phone number</label>
                                <vue-tel-input v-model="register_.phone" v-bind="{mode:'international',dynamicPlaceholder:true,inputId:'phone',enabledCountryCode:false,maxLen:15,validCharactersOnly:true,onlyCountries:['us','pk'],defaultCountry:'us',placeholder:'Enter a phone number'}" ></vue-tel-input>
                                <!-- <input v-model="register_.phone" type="text" class="form-control" id="phone" placeholder="+123-22-223-00"> -->
                            </div>
                            <div class="form-group">
                                <label for="role_title">Role</label>
                                <input v-model="register_.role_title" type="text" class="form-control" id="role_title" placeholder="Regional Manager">
                            </div>
                            <div class="form-group">
                                <multi-select :searchable="true" id="lead_source" textColumn="ls_name" placeholder="Select your lead source" v-model="register_.lsources" :options="LSources" label="Lead source"></multi-select>
                            </div>

                            <!-- <div class="form-group">
                                <label for="team_id">Team</label>
                                <Select2 id="team_id" v-model="register_.team_id" :options="teams" :settings="{ placeholder: 'Choose Team',containerCssClass:'select2-custom-container' }" />
                            </div> -->
                            <div class="form-group">
                                <multi-select :searchable="true" id="lead_permissions" textColumn="text" :options="permissions" v-model="register_.permissions" label="Permissions" placeholder="Add permissions"></multi-select>
                                <!-- <Select2 id="permissions" v-model="register_.permissions" :options="permissions" :settings="{ multiple:true,placeholder: 'Add permissions'}" /> -->
                            </div>
                            <!-- <div class="form-group">
                                <div class="Permis-btns">
                                    <a class="Permis-temp" href="#">Temporarily suspended</a>
                                    <a class="Permis-assing" href="#" data-toggle="modal" data-target="#exampleModal2" data-dismiss="modal" aria-label="Close">Assign</a>
                                </div>
                            </div> -->
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <!-- <a href="#" class="add-btn"><img class="img-fluid" src="../../assets/img/22. Delete.svg" alt=""> Cancel</a> -->
                    <button type="submit" class="rem-btn" v-if="!doCrop"><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Add</button>
                    <a href="#" v-if="doCrop" data-dismiss="modal" v-on:click="cancelCrop" class="add-btn"><img class="img-fluid" src="../../assets/img/22. Delete.svg" alt=""> Cancel</a>
                    <button type="button" v-on:click="cropIt" class="rem-btn" v-if="doCrop">Crop</button>
                </div>
                </form>
            </div>
        </div>
    </div>
    <!-- add-member Modal -->
</template>
<script>
import VueDaval from 'vue-daval';
import axios from 'axios'
import $ from 'jquery'
//import Select2 from 'v-select2-component';
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';

import MultiSelect from '../helpers/MultiSelect'

export default {
    name: 'AddTeamMember',
    components: {VueCropper,MultiSelect},
    mixins: [ VueDaval ],
    data(){
		return{
            isActive:false,
            LSources:[],
            register_:  {
                img:'',first_name:'',last_name:'',email:'',password:'',cpassword:'',phone:'',role_title:'',permissions:[],lsources:[],
                team:this.$route.params.slug
            },
            imgUrl: require('../../assets/img/profile.svg'),
            cropImgUrl: null,
            doCrop:false,
            permissions:[
                {
                    text: "Permission1",
                    id: "Permission1"
                }, {
                    text: "Permission2",
                    id: "Permission2"
                }, {
                    text: "Permission3",
                    id: "Permission3"
                },{
                    text: "Permission4",
                    id: "Permission4"
                },{
                    text: "Permission5",
                    id: "Permission5"
                },{
                    text: "Permission6",
                    id: "Permission6"
                }
            ],
		}
    },
    vdRules: {
        register_:  {
            first_name:{required: true},last_name:{required: true},
            email:{required: true, type: 'email'},
            phone:{required: true},role_title:{required: true}
        }
    },
    created(){
        this.getLeadSource()
        let vs = this
        $('body').mouseup(function(e){
            var container = $(".leadsources");
            if (!container.is(e.target) && container.has(e.target).length === 0){
                vs.isActive = false
            }
        });
	},
	methods:{
		addNewTeamMember(){
            this.$vd.register_.$validate().then(() => {
                axios
                .post('add_team_member/2',this.register_,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        $("#addMemberModal").modal('hide');
                        this.$root.$emit('newMember', res.data.success)
                        this.register_ = {
                            img:'',first_name:'',last_name:'',email:'',password:'',cpassword:'',phone:'',role_title:'',permissions:[],lsources:[],
                            team:this.$route.params.slug
                        };
                    }
                })
                .catch(err =>{
                    console.log(err)
                });

            }).catch(() => {

            });
        },
        handleFileUpload(e){
            const file = e.target.files[0];
            if (file.type.indexOf('image/') === -1) {
                alert('Please select an image file');
                return;
            }

            if (typeof FileReader === 'function') {
                const reader = new FileReader();
                reader.onload = (event) => {
                    this.cropImgUrl = event.target.result;
                    // rebuild cropperjs with the updated source
                    this.$refs.cropper.replace(event.target.result);
                };
                reader.readAsDataURL(file);
            } else {
                alert('Sorry, FileReader API not supported');
            }
            this.doCrop = true

            //this.imgUrl = URL.createObjectURL(this.$refs.file.files[0]);
        },
        cropIt(){
            this.imgUrl = this.$refs.cropper.getCroppedCanvas().toDataURL()
            this.register_.img = this.$refs.cropper.getCroppedCanvas().toDataURL()
            this.doCrop = false
        },
        cancelCrop(){
            this.doCrop = false
        },
        getTeams(){
			axios
			.get('teams',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
                const {data:{data}} = res
                const allTeams = []
                $.each(data, function(key, value) {
                    allTeams.push({id:value.tm_id,text:value.tm_name});
                });
                
                // this.$root.$on('newTeam', function(newTeam_data) {
                //     data.push(newTeam_data)
                // })
                this.teams = allTeams
			})
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadSource(){
			axios
			.get('lead_sources')
			.then( res => {
                if (res.data.success) {
                    this.LSources = res.data.success
                }
			})
			.catch(err =>{
				console.log(err)
			})
        },
    },
    watch: {
        '$route.params.slug': function () {
            this.register_.team = this.$route.params.slug
        }
    }
}
</script>
<style scoped>
.vue-tel-input{
        border: 1px solid #D5D8DF !important;
}
.vue-tel-input:focus-within{
    box-shadow:none;
    border-color: #3151A1 !important;
}
</style>