<template>
  <div class="menu-area">
    <div class="closes"><i class="fa fa-times" aria-hidden="true"></i></div>
    
    <!-- logo -->
    <div class="logo">
      <router-link to="/dashboard">
        <img class="img-fulid small-logo" src="../assets/img/logo-g.svg" alt="logo">
        <img class="img-fulid big-logo" src="../assets/img/logo.svg" alt="logo">
      </router-link>
    </div>
    <!-- logo -->
    <ul>
      <li><a class="active" href="#"><img class="img-fluid" src="../assets/img/friends.svg" alt=""> <span class="hide-item">Team </span></a>
        <!-- sub-menu -->
        <ul class="sub-menu">
          <li><h4>Teams</h4></li>
          <li v-for="team in teams"  :key="team.tm_id"><router-link :to="{ name: 'Members', params: {slug: team.tm_slug } }">{{team.tm_name}}</router-link> </li>
          <li><a href="#" data-toggle="modal" data-target="#addTeamModal">Add team</a> </li>
        </ul>
          <!-- sub-menu -->
      </li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Schedule.svg" alt=""> <span class="hide-item">Prospects </span></a></li>
      <li><router-link :to="{ name: 'Leads'}"><img class="img-fluid" src="../assets/img/Lead.svg" alt=""> <span class="hide-item">Lead Sheets </span></router-link></li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Manger.svg" alt=""> <span class="hide-item">Lead Manger </span></a></li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Taskr.svg" alt=""> <span class="hide-item">Task Manager </span></a></li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Marketing.svg" alt=""> <span class="hide-item">Marketing Settings </span></a></li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Email.svg" alt=""> <span class="hide-item">Email </span></a></li>
      <li><a href="#"><img class="img-fluid" src="../assets/img/Settings.svg" alt=""> <span class="hide-item">Settings </span></a></li>
    </ul>
    <AddTeam/>
  </div>
</template>

<script>
import axios from 'axios'
import AddTeam from '../components/teams/AddTeam'

export default {
  name: 'TheSidebar',
  components: {
      AddTeam
  },
  data(){
    return{
      teams: null,
    }
	},
	created(){
		this.getTeams()
	},
	methods:{
		getTeams(){
			axios
			.get('teams',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
				const {data:{data}} = res
        this.teams = data
        this.$root.$on('newTeam', function(newTeam_data) {
            data.push(newTeam_data)
        })
				//console.log(res)
			})
			.catch(err =>{
				console.log(err)
			})
		}
  }
}
</script>
