<template>
    <!-- saleteam-area -->
    <div class="saleteam-area">
        <!-- teams --> 
        <div class="teams" v-for="member in members"  :key="member.id">
            <div class="team-img">
                <img v-if="member.picture" class="img-fluid" :src="require('../../assets/images/profile/' + member.picture)" />
                <img v-else class="img-fluid" src="../../assets/img/profile.svg" alt="">
            </div>

            <div class="team-title">
                <h3>{{member.name}}</h3>
                <p>{{member.tm_name}}</p>
                <p>{{member.role_title}}</p>
            </div>

            <div class="team-tags">
                <p>Permission</p>
                <ul v-if="member.permissions">
                    <comaSplit v-for="(tag, i) in member.permissions.split(',')" :key="i" :tag="tag"></comaSplit>
                    <!-- <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li> -->
                </ul>
            </div>
        </div>
        <!-- teams -->
        
        <!-- teams -->
        <div class="teams">
            <div class="add-new">
                <a href="#" data-toggle="modal" data-target="#addMemberModal" data-backdrop="static" data-keyboard="false"><img class="img-fluid" src="../../assets/img/240.svg" alt=""></a>
            </div>
        </div>
        <!-- teams -->

    </div>
    <!-- saleteam-area -->
</template>

<script>
import axios from 'axios'
import comaSplit from '../helpers/comaSplit'
export default {
    name: 'MembersGrid',
    data(){
		return{
            members: {},
            team_id: null
		}
    },
    components:{
        comaSplit
    },
	created(){
		this.getTeamMembers()
    },
    watch: {
        '$route.params.slug': function () {
            this.getTeamMembers()
        }
    },
	methods:{
		getTeamMembers(){
			axios
			.get('teamMembers/'+this.$route.params.slug,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
			.then( res => {
                const data = res.data
                //if(data)
                this.members = data
                if(data.error){
                    if(data.error == 'no_access'){
                        this.$router.push('/dashboard')
                    }
                }else{
                    this.$root.$on('newMember', function(newMember_data) {
                        data.push(newMember_data)
                    })
                }
			})
			.catch(err =>{
				console.log(err)
			})
		}
    }
}
</script>