<template>
    <!-- add-member Modal -->
    <div class="modal fade" id="addTeamModal" tabindex="-1" role="dialog" aria-labelledby="addTeamModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="addTeamModalLabel">Add New Team</h5>
                    <a class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                    </a>
                </div>
                <form v-on:submit.prevent = "addNewTeam">
                <div class="modal-body">
                    <div class="add-member">
                    
                        <div class="form-group">
                            <label for="teamName">Team Name</label>
                            <input type="text" v-model="teamName" class="form-control" id="teamName" placeholder="Enter team name">
                        </div>
                    
                    </div>
                </div>
                <div class="modal-footer">
                    <!-- <a href="#" class="add-btn"><img class="img-fluid" src="../../assets/img/22. Delete.svg" alt=""> Cancel</a> -->
                    <button type="submit" class="rem-btn"><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Add</button>
                </div>
                </form>
            </div>
        </div>
    </div>
    <!-- add-member Modal -->
</template>
<script>
import VueDaval from 'vue-daval';
import axios from 'axios'
import $ from 'jquery'


export default {
    name: 'AddTeam',
    mixins: [ VueDaval ],
    data(){
		return{
            teamName:  ''
		}
    },
    vdRules: {
        teamName: {required: true}
    },
	methods:{
		addNewTeam(){
            this.$vd.teamName.$validate().then(() => {
                axios
                .post('addTeam',{name:this.teamName},{headers: {Authorization:'Bearer '+this.$storage.get('auth').token,'X-Requested-With':'XMLHttpRequest'}})
                .then( res => {
                    if (res.data.success) {
                        //console.log(res.data.success)
                        $("#addTeamModal").modal('hide');
                        this.$root.$emit('newTeam', res.data.success)
                        this.teamName = '';
                        this.$router.push({
                            name: 'Members', 
                            params: { slug: res.data.success.tm_slug }
                        });
                    }
                })
                .catch(err =>{
                    console.log(err)
                });

            }).catch(() => {

            });
        }
    }
}
</script>